<template>
  <div class="operations">
      <p class="operations-header">{{isDemo ? $localizeFilter('operations_on_account_demo') : $localizeFilter('operations_on_account')}}</p>
      <OperationsTable 
      :showTable="operations.length > 0"
      :operations="operations" :showOptions="false" @changeStatus='changeStatus' :decimals="decimals"/>
      <EmptyTable v-if="operations.length == 0" :text="$localizeFilter('no_operations')"/>
      <pagination v-model="page" :records="count" :per-page="pageSize" @paginate="pageChangeHandler"/>
  </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import OperationsTable from '@/components/OperationsTable'
import paginationMixin from '@/mixins/pagination.mixin'
import localizeFilter from "@/filters/localize.filter"
import EmptyTable from "@/components/EmptyTable"

export default defineComponent({
  props: ['type','showOptions', 'coinTicker', 'coinWalletData'],
  components: {
    OperationsTable, EmptyTable
  },
  mixins: [paginationMixin],
  watch:{
      page() {
          this.$nextTick(this.loadData);
      },
  },
  setup() {
    
  },
  data: () => ({
    // operations: [],
    count: 0,
    status: 'all',
    operations: []
  }),
  computed: {
    isDemo() {
      return this.$store.getters.isDemo
    },
    decimals() {
      return this.coinWalletData?.decimals || 8
    },
  },
  mounted() {
    this.loadData()
  },
  methods: {
    changeStatus(status) {
      this.status = status
      this.loadData()
    },
    async loadData() {
      // if(this.demoUSDV){
      //   this.operations.push()
      // }
        try {
            const data = {status: this.status, coinTicker: this.coinTicker, skip: this.skip, limit: this.limit}
            this.operations = await this.$store.dispatch('getWalletOperationsByFilter', data)
            const {count} = await this.$store.dispatch('getWalletOperationsByFilterCount', data)
            this.count = count
            this.setupPagination(this.operations)
        } catch(e) {
            this.$error(e)
        } 
     }
  }
})
</script>

<style scoped>
.operations {
  display: flex;
  flex-direction: column;
}
.operations-header {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  text-align: left;
  margin-bottom: 12px;
  margin-top: 26px;
}

@media(min-width: 1024px) {
  .operations {
    align-items: flex-start;
    justify-content: flex-start;
  }
}

@media(max-width: 1024px) {
  .operations {
    align-items: center;
    justify-content: flex-start;
  }
}
</style>
